@import url(https://fonts.googleapis.com/css?family=Raleway:100,200,300,regular,500,600,700,800,900,100italic,200italic,300italic,italic,500italic,600italic,700italic,800italic,900italic);
@import url(https://fonts.googleapis.com/css?family=Tektur:regular,500,600,700,800,900);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #f0f0f0;
  color: #212529;
  font-family: Raleway, sans-serif;
}

.wrapper {
  display: flex;
  flex-direction: column;
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  height: 100vh;
}

a {
  text-decoration: none;
  color: #b09361;
}
a:hover {
  color: #49729c;
  cursor: pointer;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 10px 20px;
  background: #212529;
  color: #f0f0f0;
  font-family: Tektur, sans-serif;
}
@media screen and (max-width: 992px) {
  .header h1 {
    font-size: 1.3rem;
  }
}
.header-navigation {
  display: flex;
  justify-content: center;
  flex: 1;
}
@media screen and (max-width: 768px) {
  .header-navigation {
    display: none;
  }
}
.header-nav {
  display: flex;
  list-style: none;
  font-size: 1.5rem;
}
.header-nav li {
  margin: 0 20px;
}
@media screen and (max-width: 992px) {
  .header-nav li {
    margin: 0 10px;
  }
}

.main {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 20px;
  border-right: 2px solid #212529;
  border-left: 2px solid #212529;
}

.pte {
  display: flex;
  justify-content: center;
  width: 100%;
}
@media screen and (max-width: 768px) {
  .pte {
    flex-direction: column;
    align-items: center;
    justify-content: start;
  }
}

.swiper {
  display: flex;
  width: 100%;
}
@media screen and (max-width: 962px) {
  .swiper {
    min-width: 40%;
  }
}
@media screen and (max-width: 768px) {
  .swiper {
    max-height: 20%;
  }
}
@media screen and (max-width: 640px) {
  .swiper {
    max-height: 17%;
  }
}
.swiper-slide {
  width: 100%;
}
.swiper-slide img {
  width: 100%;
  object-fit: cover;
}
@media screen and (max-width: 768px) {
  .swiper-slide img {
    object-position: center -190px;
  }
}
@media screen and (max-width: 640px) {
  .swiper-slide img {
    object-position: center -75px;
  }
}
@media screen and (max-width: 374px) {
  .swiper-slide img {
    object-position: center -85px;
  }
}

.info {
  padding-left: 20px;
}
@media screen and (max-width: 768px) {
  .info {
    padding-left: 0;
    margin-top: 20px;
  }
}
.info p {
  margin-bottom: 10px;
  line-height: 130%;
}
.info p a {
  color: #705322;
  text-decoration: underline;
}
.info p span {
  padding: 1px 5px;
  background: #ffdb8b;
}

.contacts {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 10px 20px;
  border-right: 2px solid #212529;
  border-left: 2px solid #212529;
}
.contacts input {
  padding: 5px;
  margin: 5px 0;
}

.message {
  padding: 5px;
}

.footer {
  display: flex;
  align-items: center;
  height: 60px;
  padding: 10px 20px;
  background: #212529;
  color: #f0f0f0;
}